.container-suggestion {
    margin-top: 20px;
}
.header-suggestion {
    display: flex;
    align-items: center;
    gap: 20px;
}

.header-suggestion img {
    height: 56px;
    width: 56px;
    border-radius: 100%;
    cursor: pointer;
}

.user-info-suggestion {
    display: flex;
    align-items: center;
    justify-content: space-between;

    flex: 1;
}

.user-info-suggestion .infos span {
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.user-info-suggestion .infos p {
    font-size: 13px;
    font-weight: 400;
    cursor: pointer;
    color: var(--secondary);
}

.user-info-suggestion .switch {
    font-weight: 500;
    color: var(--quartary);

    cursor: pointer;
}

.header-main-suggestion {
    display: flex;
    justify-content: space-between;
    margin: 15px 0 10px 0;
}

.header-main-suggestion p {
    font-size: 15px;
    color: var(--secondary);
    font-weight: 500;
    cursor: pointer;
}

.header-main-suggestion span {
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
}

.user-suggestion {
    margin-top: 5px;
}

.infos-suggestion {
    margin: 15px 0 0 5px;
    display: flex;
    align-items: center;
    gap: 16px;
}

.infos-suggestion img {
    width: 36px;
    height: 36px;
    border-radius: 100%;
    cursor: pointer;
}

.info-suggestion {
    flex: 1;
}

.infos-suggestion span {
    font-size: 15px;
    font-weight: 600;

    cursor: pointer;
}

.infos-suggestion p {
    font-size: 13px;
    margin-top: 1px;
    color: var(--secondary);
}

.infos-suggestion .follow {
    font-size: 12px;
    font-weight: 500;
    color: var(--quartary);
    
    cursor: pointer;
}

.footer-suggestion {
    flex: 1;
    margin-top: 25px;
}

.footer-suggestion p{
    color: var(--tertiary);
    font-size: 12px;
    margin-bottom: 15px;
    cursor: pointer;
    
}

.header-user-info-suggestion {
    display: flex;
}

.user-info-1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #131313;
    border-radius: 5px;
    padding: 2px;
    margin-top: 10px;
    width: 100px;
    max-width: 100px;

}

.user-info-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #131313;
    border-radius: 5px;
    padding: 2px;
    margin-top: 10px;
    width: 100px;
    max-width: 100px;

}

.user-info-3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #131313;
    border-radius: 5px;
    padding: 2px;
    margin-top: 10px;
    width: 100px;
    max-width: 100px;
}

.user-info-1 span {
    font-size: 14px;
    font-weight: 200;
}

.user-info-2 span {
    font-size: 14px;
    font-weight: 200;
}

.user-info-3 span {
    font-size: 14px;
    font-weight: 200;
}