* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    border: 0;
    outline: 0;
    background: none;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
}

html, body, #root {
    max-width: 100vw;
    max-height: 100vw;

    width: 100%;
    height: 100%;
}

html {
    background: var(--background-main);
}

p, span {
    color: var(--primary);
}

a:link, a:visited {
    color: var(--primary);
    text-decoration: none;
    cursor: pointer;
  }

.react-icon {
    color: var(--primary);
}

:root {
    --background-main: #000000;
    --background: #161616;
    --primary: #ffffff;
    --secondary: #c5c5c5;
    --tertiary: #c7c7c7;
    --quartary: #0095f6;
    --border: #1f1f1f;
    --border-secondary: #efefef;




}